.intro{
    display: flex;
    height: 77vh;
    width: 95%;
    margin: auto;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    margin-top: 10px;
    margin-bottom: 30px;
}
.left{
    display: flex;
    position: relative;
    flex-direction: column;
    gap: 2;
    max-width: 55%;
}
.name{
    display: flex;
    flex-direction:column;
    gap: 1rem;
   
    padding-left: 13%;
}
.name>:nth-child(2)
{
    color: var(--black);
    font-weight:bold;
    font-size: 2rem;

}
.name>:nth-child(3)
{
    color: var(--orange);
    font-weight:normal;
    font-size: 2.5rem;
}
.name>:nth-child(4)
{
  font-weight: 100;
    color: var(--gray);
    font-weight:semi-bold;
    font-size: 1.2rem;
   
}
.hire{
  display: flex;
  margin-top:20px;
  gap: 2rem;
  padding-left: 13%;
 
}

   .icons {
    margin-top: 25px;
    display: flex;
    gap: 0rem;
 
  }
  
  .icons > * {
    transform: scale(0.70);
  }
  .icons > *:hover {
    cursor: pointer;
  }



.right{
    width:40%;
   margin: auto;
}

.my_img{
  width: 400px;
border-color: burlywood;
border-radius: 15px;
}